.Main-nav {
  padding        : 0;
  width          : 100%;
  height         : 50%;
  display        : flex;
  justify-content: center;
}

.Main-nav-list {
  margin-right: 10px;
}

.Trends-film-title {
  font-size  : 24px;
  font-weight: bold;
  text-align : center;
}

.Navigation-button-wrapper {
  text-align: center;

}

.Navigation-button {
  margin-right : 10px;
  margin-bottom: 10px;
  padding      : 10px 10px;
  width        : 150px;
  font-size    : 20px;
  border       : none;
  outline      : none;
  transition   : 0.5s;
}

.Navigation-button:hover {
  background-color: grey;
  color           : #fff;
  transition      : 0.5s;
}

.Movie-Ditails-Wrapper {
  padding: 30px;
}

.Movie-ditails {
  display       : flex;
  padding-bottom: 15px;
  border-bottom : 3px solid grey;
}

.Movie-ditails-img {
  margin-right: 30px;
}

.Aditional-information-wrapper {
  border-bottom: 3px solid grey;

}

/*
 * Стили компонента SearchForm
 */


.SearchForm-button {
  width     : 70px;
  height    : 100%;
  opacity   : 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor    : pointer;
  outline   : none;
  font-size : 16px;
  color     : black;
}

.SearchForm-button:hover {
  opacity: 1;
}

.SearchForm-input {
  margin-right: 5px;
  width       : 300px;
  height      : 30px;
  font-size   : 16px;
  color       : black;
}

.SearchForm-input::placeholder {
  font     : inherit;
  font-size: 18px;
}

/*
 * Стили компонента ImageGallery
 */

.error-title {
  padding    : 0 24px;
  margin     : 0;
  font-weight: bold;
  font-size  : 24px;
}

.ImageGallery {
  display              : grid;
  max-width            : 1340px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap             : 16px;
  margin-top           : 10px;
  margin-bottom        : 0;
  padding              : 0;
  list-style           : none;
  margin-left          : auto;
  margin-right         : auto;
}

/*
 * Стили компонента ImageGalleryItem
 */
.Container {
  padding         : 0 0 10px 0;
  transition      : 1s;
  transition-delay: 1s;
}

.Loader {
  padding-top        : 50px;
  text-align         : center;
  /* background-color: black; */
}

.ErrorMessage {
  display   : block;
  text-align: center;
  color     : #3f51b5;
}

.ImageGalleryItem {
  transition-delay: 1s;
  border-radius   : 2px;
  box-shadow      : 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.ImageGalleryItem-image {
  transition      : 1s;
  transition-delay: 1s;
  width           : 100%;
  height          : 260px;
  object-fit      : cover;
  box-shadow      : 0px 1px 3px 0px rgba(0, 0, 0, 0.2);
  transition      : transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ImageGalleryItem-image:hover {
  transition: 1s;
  transform : scale(1.03);
  cursor    : zoom-in;
}